<template>
  <!--  -->
  <div v-show="ViewLayersLightbox" class="ViewLayersLightbox">
    <div class="ViewLayersLightbox_grid1">
      <div class="click_content_head">
        <p>
          <span v-if="memo.kind === 'memo'">  </span>
          <span v-else-if="memo.kind === 'video'">  </span>
          <span v-else-if="memo.kind === 'photo'">  </span>
          
        </p>
        <button class="hiddenBtn" title="">
          <img
            src="@/assets/close05.png"
            class="btnImg"
            @click="close_button()"
          />
        </button>
      </div>
    </div>
    <div class="ViewLayersLightbox_grid2">
      <!--  -->
      <textarea
        ref="view_text"
        v-if="show_text_box"
        id="view_content_text"
        class="lightbox_content textarea"
        v-model="view_content_text"
        readonly
      />
      <!--  -->
      <video
        ref="view_video"
        v-if="show_video_box"
        id="view_content_video"
        class="lightbox_content"
        v-bind:class="{ lightbox_video: video_vertical }"
        :src="'/memo/' + memo.id + '.webm'"
        :poster="'/memo/' + memo.id + '_thumb.jpg'"
        controls
        controlslist="nodownload noremoteplayback"
        disablePictureInPicture
        playsinline
      />
      <!--  -->
      <img
        ref="view_photo"
        v-if="show_photo_box"
        id="view_content_photo"
        class="lightbox_content"
        v-bind:class="{ lightbox_img: img_vertical }"
        :src="'/memo/' + memo.id + '.jpg'"
      />
    </div>
    <div class="ViewLayersLightbox_grid3">
      <div class="grid3_float">
        <div class="info_memo">
          <p> {{ memo.name }}</p>
          <p> {{ memo.date }}</p>
        </div>
      </div>
      <div class="grid3_float">
        <div class="info_memo">
          <p> {{ memo.lat }}</p>
          <p> {{ memo.lng }}</p>
        </div>
      </div>
      <!--  -->
      <div class="grid3_float">
        <!-- <div class="info_classify">
          <p v-if="memo.classify">{{ memo.classify }}</p>
        </div> -->
        <div class="info_classify">
          <p class="memoclassify_header"></p>
          <b-form-select
            class="classify_select"
            v-model="classify_name"
            v-on:change="classify_change"
            :options="classifyListObj"
            v-bind:disabled="!memo.display_flag"
          ></b-form-select>
        </div>
      </div>
      <!--  -->
      <div class="grid3_float">
        <div class="info_status">
          <p class="memostatus_header"></p>
          <p
            class="memostatus_status"
            @click="memo_status()"
            v-bind:disabled="!memo.display_flag"
          >
            <img
              v-if="memo.status == ''"
              src="./assets/ico_check_on.png"
              alt=""
              class="select_yes"
            />
            <img
              v-else
              src="./assets/ico_check_off.png"
              alt=""
              class="select_no"
            />
          </p>
        </div>
      </div>

      <div class="grid3_textarea" v-if="memo.kind !== 'memo'">
        <p class="info_text_area">
          {{ memo.info }}
        </p>
      </div>

      <div class="grid3_absolute">
        <p class="download_can" @click="memo_download">
          <!--  -->
          <span v-if="show_text_box && memo_downloadable">
            <a
              :href="'/memo/' + memo.id + '.txt'"
              download
              title=""
            >
              <img src="./assets/ico_download.png" alt="" />
            </a>
          </span>
          <!--  -->
          <span v-if="show_photo_box && memo_downloadable">
            <a
              :href="'/memo/' + memo.id + '.jpg'"
              download
              title=""
            >
              <img src="./assets/ico_download.png" alt="" />
            </a>
          </span>
          <!--  -->
          <span v-if="show_video_box && memo_downloadable">
            <a
              :href="'/memo/' + memo.id + '.webm'"
              download
              title=""
            >
              <img src="./assets/ico_download.png" alt="" />
            </a>
          </span>
        </p>
        <p
          v-show="memo.display_flag"
          class="trash_can"
          @click="memo_delete"
          title=""
        >
          <img src="./assets/ico_trash.png" alt="" />
        </p>
      </div>
    </div>

    <modalDialog v-if="show_msg_dialog">
      <span slot="header"></span>
      <span slot="body" style="color: #333">  </span>
      <div slot="footer">
        <button class="btn btn-primary" @click="back_memo()"></button>
        <button class="btn btn-primary" @click="delete_memo()"></button>
      </div>
    </modalDialog>
  </div>
</template>

<script>
/* eslint-disable */
import $store from "@/store";
import { memoEvent } from "./index.js";

import ModalDialog from "@/components/ModalDialog.vue";

export default {
  data() {
    return {
      ViewLayersLightbox: false,
      memo: [],
      show_photo_box: false,
      show_video_box: false,
      show_text_box: false,
      classify_name: "",
      field_width: window.innerWidth,
      field_height: window.innerHeight,
      show_msg_dialog: false,
    };
  },
  components: {
    modalDialog: ModalDialog,
  },
  mounted() {
    memoEvent.$on("MEMO_VIEW2", (data) => {
      // 
      this.$store.dispatch("memo/fetchClassify");
      this.ViewLayersLightbox = true;
      this.memo = data;
      console.log(JSON.stringify(data));

      // 
      // this.classify_name = this.memo.classify;
      this.classify_name = this.memo.classify;

      // html
      if (data.kind == "video") {
        this.show_photo_box = false;
        this.show_video_box = true;
        this.show_text_box = false;
      } else if (data.kind == "photo") {
        this.show_photo_box = true;
        this.show_video_box = false;
        this.show_text_box = false;
      } else {
        this.view_content_text = data.info;
        this.show_photo_box = false;
        this.show_video_box = false;
        this.show_text_box = true;
      }
    });

    // 
    if (window.innerHeight > window.innerHeight) {
      // ()
      // grid-template-rows: 9% 41 50%;
      // grid-template-columns: 5% 90% 5%;
      this.field_width = window.innerWidth * 0.9;
      this.field_height = window.innerHeight * 0.41;
    } else {
      // PC()
      // grid-template-rows: 9% 62% 29%;
      // grid-template-columns: 15% 70% 15%;
      this.field_width = window.innerWidth * 0.7;
      this.field_height = window.innerHeight * 0.62;
    }
    // risize
    window.addEventListener("resize", this.windowResize);

    memoEvent.$on(
      "ALL_CLOSE_MEMO",
      function () {
        const click_targets = this.$store.state.click_targets;
        if (0 != click_targets.length) {
          // 
          this.ViewLayersLightbox = false;

          // 
          for (let key in this.$store.state.memo_update) {
            if (0 === Object.keys(this.$store.state.memo_update[key]).length) {
              // 
              memoEvent.$emit("DEL_MEMO", key);
            } else {
              memoEvent.$emit(
                "UPDATE_MEMO",
                this.$store.state.memo_update[key]
              );
            }
          }
          // 
          this.$store.commit("resetMemoUpdate");

          // 
          this.$store.commit("resetClickTargets");
          this.$store.commit("hiddenClickList");
          console.log("");
        }
      }.bind(this)
    );
  },
  methods: {
    close_button() {
      const videoElem = document.getElementById("view_content_video");
      if (videoElem && !videoElem.paused) {
        // 
        videoElem.pause();
        videoElem.currentTime = 0;
      }
      this.ViewLayersLightbox = false;

      const click_targets = this.$store.state.click_targets;
      if (1 == click_targets.length) {
        // 
        for (let key in this.$store.state.memo_update) {
          if (0 === Object.keys(this.$store.state.memo_update[key]).length) {
            // 
            memoEvent.$emit("DEL_MEMO", key);
          } else {
            memoEvent.$emit("UPDATE_MEMO", this.$store.state.memo_update[key]);
          }
        }
        // 
        this.$store.commit("resetMemoUpdate");

        // 1
        this.$store.commit("resetClickTargets");
        this.$store.commit("hiddenClickList");
      } else {
        // 
        this.$store.commit("displayClickList");
      }
    },
    memo_delete() {
      // 
      this.show_msg_dialog = true;
    },
    back_memo() {
      // Cancel
      this.show_msg_dialog = false;
    },
    delete_memo() {
      // 
      this.$store.commit("resetMemoUpdateDetail", this.memo.id);

      const result = this.$store.state.click_targets.filter(
        (click_target) => click_target.id !== this.memo.id
      );
      this.$store.commit("setClickTargets", result);
      console.log(result);

      // 
      this.show_msg_dialog = false;

      // 
      this.ViewLayersLightbox = false;

      // 
      for (let key in this.$store.state.memo_update) {
        if (0 === Object.keys(this.$store.state.memo_update[key]).length) {
          // 
          memoEvent.$emit("DEL_MEMO", key);
          console.log(key);
        } else {
          memoEvent.$emit("UPDATE_MEMO", this.$store.state.memo_update[key]);
        }
      }
      // 
      this.$store.commit("resetMemoUpdate");
      // 
      this.$store.commit("resetClickTargets");
      this.$store.commit("hiddenClickList");
    },
    memo_download() {
      console.log("");
    },
    classify_change() {
      // 

      this.memo.classify = this.classify_name;

      // 
      this.$store.commit("setMemoUpdate", this.memo);
      // console.log(this.$store.state.memo_update);

      //
      this.$store.commit("memo/setClickedMemo", this.memo);

      console.log(JSON.stringify(this.memo));
    },
    memo_status() {
      if (!this.memo.display_flag) {
        // 
        return;
      }

      if (this.memo.status == "") {
        this.memo.status = "";
      } else if (this.memo.status == "") {
        this.memo.status = "";
      }
      // 
      // this.$store.state.socketio.emit("upMemo", JSON.stringify(this.memo));

      // 
      const result = this.$store.state.click_targets.find(
        (click_target) => click_target.id === this.memo.id
      );
      const result2 = this.$store.state.click_targets.filter(
        (click_target) => click_target.id !== this.memo.id
      );
      // 
      let memo_image =
        this.memo.status == ""
          ? this.memo.kind == "memo"
            ? "/images/memo_icon.png"
            : this.memo.kind == "photo"
            ? "/images/photo_icon.png"
            : "/images/video_icon.png"
          : this.memo.kind == "memo"
          ? "/images/memo_red_icon.png"
          : this.memo.kind == "photo"
          ? "/images/photo_red_icon.png"
          : "/images/video_red_icon.png";
      result.icon = memo_image;
      result2.unshift(result);
      this.$store.commit("setClickTargets", result2);

      // 
      this.$store.commit("setMemoUpdate", this.memo);

      //
      this.$store.state.memo.clicked_memo[this.memo.id] = this.memo;
    },
    windowResize() {
      // .ViewLayersLightbox
      if (window.innerHeight > window.innerWidth) {
        // ()
        // grid-template-rows: 9% 41 50%;
        // grid-template-columns: 5% 90% 5%;
        this.field_width = window.innerWidth * 0.9;
        this.field_height = window.innerHeight * 0.41;
      } else {
        // PC()
        // grid-template-rows: 9% 62% 29%;
        // grid-template-columns: 15% 70% 15%;
        this.field_width = window.innerWidth * 0.7;
        this.field_height = window.innerHeight * 0.62;
      }
    },
  },
  computed: {
    classifyListObj() {
      // 
      // 
      let options = [{ value: "", text: "" }]; // ()
      let obj = {};
      this.$store.state.memo.classify_list.forEach((data, i) => {
        if (data) {
          obj = { value: data, text: data };
          options.push(obj);
        }
      });
      return options;
    },
    img_vertical() {
      var path = "/memo/" + this.memo.id + ".jpg";
      var element = new Image();
      element.src = path;
      // 
      var img_rate = element.naturalWidth / element.naturalHeight;
      // 
      var field_rate = this.field_width / this.field_height;
      element.onload = function () {
        // 
        img_rate = element.naturalWidth / element.naturalHeight;
        if (img_rate > field_rate) {
          return true;
        } else {
          return false;
        }
      };
      if (img_rate > field_rate) {
        // (/)
        return true;
      } else {
        // (/)
        return false;
      }
    },
    video_vertical() {
      var path = "/memo/" + this.memo.id + ".webm";
      var thumb = "/memo/" + this.memo.id + "_thumb.jpg";
      var element = document.createElement("video");
      // 
      var video_rate = element.videoWidth / element.videoHeight;
      // 
      var field_rate = this.field_width / this.field_height;
      element.onloadedmetadata = function () {
        // 
        video_rate = element.videoWidth / element.videoHeight;
        if (video_rate > field_rate) {
          return true;
        } else {
          return false;
        }
      };
      element.src = path;
      element.poster = thumb;
      element.load();
      if (video_rate > field_rate) {
        // (/)
        return true;
      } else {
        // (/)
        return false;
      }
    },
    memo_downloadable() {
      return this.$store.getters["memo_downloadable"];
    },
  },
  beforeDestroy() {
    // 
    window.removeEventListener("resize", this.windowResize);
  },
};
</script>

