import $store from "@/store";
import ManageComponent from "./ManageLayer.vue";
import $ from "jquery";

import Vue from "vue";
export const manageEvent = new Vue();

export function ManageLayer() {
  const noop = () => {};

  function add() {
    // map
    manageEvent.$on("MANAGE_BACK", () => this.setActive(false));
  }

  function setActive(active) {
    if (active) {
      $store.commit("amt/addComponent", ManageComponent);
      $store.commit("amt/setSubMenu", "ManageLayer");
    } else {
      $store.commit("amt/removeComponent", ManageComponent);
      $store.commit("amt/setSubMenu", null);
    }
  }

  function setOpacity(val) {
    // mapvector
    // 
    $("#manageLayer_container").css("opacity", val);
  }

  return {
    add, // 
    remove: noop, // 
    update: noop, // 
    setOpacity, // 
    setVisible: noop, // 
    setActive, // 
  };
}
