<template>
  <div>
    <div id="searchComponent" v-if="isSearchComponent">
      <div id="searchComponentTitle">
        <p class="searchTitle"></p>
        <button class="hiddenBtn" title="">
          <img
            src="./assets/minus.png"
            class="btnImg"
            @click="minimize_button()"
            style="width: 100%"
          />
        </button>
        <button class="closeBtn" title="">
          <img
            src="./assets/close.png"
            class="btnImg"
            @click="close_button()"
          />
        </button>
      </div>
      <div id="searchComponentBody">
        <div>
          <p class="searchDateTitle"></p>
          <div class="align_center">
            <label class="searchDate">
              <input
                type="datetime-local"
                class="inputDate"
                v-model="startDate"
              />
            </label>
          </div>
        </div>

        <div>
          <p class="searchDateTitle"></p>
          <div class="align_center">
            <label class="searchDate">
              <input
                type="datetime-local"
                class="inputDate"
                v-model="endDate"
              />
            </label>
          </div>
        </div>

        <div>
          <p class="searchDateTitle"></p>
          <div class="align_center">
            <!--  -->
            <label class="searchDevice" @click="clickDevice()">
              <p>{{ displayDevice }}</p>
            </label>
          </div>
        </div>

        <div>
          <p class="searchDateTitle"></p>
          <div class="align_center">
            <!--  -->
            <label class="searchDevice" @click="clickLayer()">
              <p v-if="layers.length">
                <template v-for="layer in layers"> {{ layer.name }}, </template>
              </p>
              <p v-else></p>
            </label>
          </div>
        </div>

        <div>
          <p class="searchDateTitle"></p>
          <div class="align_center">
            <label class="searchDevice">
              <textarea
                name=""
                id=""
                cols="30"
                rows="10"
                v-model="textArea"
              ></textarea>
            </label>
          </div>
        </div>

        <div class="searchButton">
          <button @click="search_start"></button>
        </div>
      </div>
    </div>

    <searchComponentList />

    <!--  -->
    <modalDialog v-if="show_msg_dialog">
      <span slot="header"></span>
      <span slot="body" style="color: #333">
        {{ error_message }}
      </span>
      <div slot="footer">
        <button class="btn btn-primary" @click="close_msg_dialog()">
          
        </button>
      </div>
    </modalDialog>

    <!--  -->
    <div v-if="searchSelectDevice">
      <searchComponentDevice />
    </div>

    <!--  -->
    <div v-if="searchSelectLayer">
      <searchComponentLayer />
    </div>

    <!--  -->
    <div v-if="searchComponentSmall">
      <searchComponentSmall />
    </div>
  </div>
</template>

<script>
import { searchEvent } from "./index.js";
import SearchComponentList from "./SearchComponentList";
import SearchComponentDevice from "./SearchComponentDevice";
import SearchComponentLayer from "./SearchComponentLayer";
import SearchComponentSmall from "./SearchComponentSmall";

import ModalDialog from "@/components/ModalDialog.vue";

import moment from "moment";

import axios from "axios";

export default {
  data() {
    return {
      show_msg_dialog: false,
      error_message: "",
      startDate: moment().add(-7, "days").format("YYYY-MM-DDTHH:mm:ss"),
      endDate: moment().format("YYYY-MM-DDTHH:mm:ss"),
      textArea: "",
    };
  },
  async mounted() {
    // 
    const url = "/api/usersAmt/";
    const ctx = await axios.get(url);
    // res  user_typeamtuser
    let res = ctx.data;
    this.$store.commit("amt/setSearchTargetDevice", res);

    searchEvent.$on("close", () => {
      this.close_button();
    });
  },
  components: {
    searchComponentList: SearchComponentList,
    searchComponentDevice: SearchComponentDevice,
    searchComponentLayer: SearchComponentLayer,
    searchComponentSmall: SearchComponentSmall,
    modalDialog: ModalDialog,
  },
  methods: {
    returnValeuDevices($event) {
      let opt = Object.values($event.target.options).filter((ele) => {
        return ele.selected;
      });
      let user_id = opt.map((ele) => ele.user_id);
      console.log(user_id);
    },
    returnValeuLayers($event) {
      let opt = Object.values($event.target.options).filter((ele) => {
        return ele.selected;
      });
      let user_id = opt.map((ele) => ele.user_id);
      console.log(user_id);
    },
    search_start() {
      // VecrtorLayer
      searchEvent.$emit("reset_track");
      searchEvent.$emit("reset_memo");
      // 
      this.$store.dispatch("amt/resetSearchTrack");
      this.$store.dispatch("amt/resetSearchMemo");

      if (!this.startDate) {
        this.show_msg_dialog = true;
        this.error_message = "";
        return;
      }
      let start = this.startDate.toString();
      start = moment(start).utc().format("YYYY-MM-DD HH:mm:ssZ");

      if (!this.endDate) {
        this.show_msg_dialog = true;
        this.error_message = "";
        return;
      }
      let end = this.endDate.toString();
      end = moment(end).utc().format("YYYY-MM-DD HH:mm:ssZ");

      if (!this.$store.getters["amt/isSearchComponentList"]) {
        // 2021-07-06
        // 
        this.$store.dispatch("amt/showSearchComponentList");
      }

      if (this.devices.length == 0) {
        this.show_msg_dialog = true;
        this.error_message = "";
        return;
      }
      if (this.layers.length == 0) {
        this.show_msg_dialog = true;
        this.error_message = "";
        return;
      }
      // 
      // 
      this.$store.dispatch("amt/setLayerVisible", {
        layerName: "MemoLayer",
        visible: false,
      });
      // 
      this.$store.dispatch("amt/setLayerVisible", {
        layerName: "TrackLayer",
        visible: false,
      });

      let device_id = [];
      for (let device of this.devices) {
        // 
        if (
          this.$store.getters["amt/searchLayer"].find(
            (element) => element.name === ""
          )
        ) {
          this.$store
            .dispatch("amt/fetchTrack", {
              userId: device.id,
              begin: start,
              end: end,
            })
            .then((data) => {
              console.log(data);
              this.$store
                .dispatch("amt/setSearchTrack", {
                  userId: device.id,
                  track: data,
                })
                .then(function () {
                  searchEvent.$emit("searchedTrack");
                });
            });
        }
        device_id.push(device.id);
      }
      // 
      if (
        this.$store.getters["amt/searchLayer"].find(
          (element) => element.name === ""
        )
      ) {
        this.$store
          .dispatch("amt/fetchMemo", {
            userId: device_id,
            begin: start,
            end: end,
            text: this.textArea,
          })
          .then((data) => {
            console.log(data);
            this.$store
              .dispatch("amt/setSearchMemo", {
                userId: device_id,
                memo: data,
              })
              .then(function () {
                searchEvent.$emit("searchedMemo");
              });
          });
      }
    },
    clickDevice() {
      // 
      this.$store.dispatch("amt/showSearchComponentDevice");
      // 
      this.$store.dispatch("amt/hideSearchComponent");
    },
    clickLayer() {
      // 
      this.$store.dispatch("amt/showSearchComponentLayer");
      // 
      this.$store.dispatch("amt/hideSearchComponent");
    },
    minimize_button() {
      // 
      this.$store.dispatch("amt/showSearchComponentSmall");
      this.$store.dispatch("amt/hideSearchComponent");
    },
    close_button() {
      this.$store.dispatch("amt/hideSearchComponent");
      this.$store.dispatch("amt/resetSearchTrack");
      this.$store.dispatch("amt/resetSearchMemo");
      searchEvent.$emit("reset_track");
      searchEvent.$emit("reset_memo");
      // 
      this.$store.commit("amt/setSubMenu", null);

      const layer = this.$store.getters["amt/layers"];
      const track_layer = layer["TrackLayer"];
      // ()
      this.$store.dispatch("amt/setLayerVisible", {
        layerName: "TrackLayer",
        visible: track_layer.visible,
      });
      const memo_layer = layer["MemoLayer"];
      // ()
      this.$store.dispatch("amt/setLayerVisible", {
        layerName: "MemoLayer",
        visible: memo_layer.visible,
      });
      // 
      this.$store.dispatch("amt/resetSearchDevice");
      this.$store.dispatch("amt/resetSearchLayer");

      // 
      searchEvent.$emit("SEARCH_END");
    },
    close_msg_dialog() {
      this.show_msg_dialog = false;
    },
  },
  computed: {
    isSearchComponent: function () {
      return this.$store.getters["amt/isSearchComponent"];
    },
    devices: function () {
      return this.$store.getters["amt/searchDevice"];
    },
    displayDevice: function () {
      let dvice = "";
      if (this.$store.getters["amt/searchDevice"].length !== 0) {
        this.$store.getters["amt/searchDevice"].forEach((element) => {
          dvice = dvice + element.name + ",";
        });
        if (dvice.length >= 20) {
          dvice = dvice.substr(0, 19) + "...";
        }
      } else {
        dvice = "";
      }
      return dvice;
    },
    layers: function () {
      return this.$store.getters["amt/searchLayer"];
    },
    searchSelectDevice: function () {
      return this.$store.getters["amt/isSearchSelectDevice"];
    },
    searchSelectLayer: function () {
      return this.$store.getters["amt/isSearchSelectLayer"];
    },
    searchComponentSmall: function () {
      return this.$store.getters["amt/isSearchComponentSmall"];
    },
  },
};
</script>

