/* eslint-disable */
import { Map, View, Overlay, Feature } from "ol";
import * as olLayer from "ol/layer";
import * as olControl from "ol/control";
import * as olInteraction from "ol/interaction";
import * as olProj from "ol/proj";
import * as olSource from "ol/source";
import * as olStyle from "ol/style";
import * as olGeom from "ol/geom";

//
// import config from "./config.json";
//const config = {
//  dataFile: "/moduleData/memoLayerData.json"
//};

import $store from "@/store";
import memoLayerPopup from "./MemoLayerPopup.vue";
import memoMake from "./MemoMake.vue";
// import memoMake from "./MovieMake.vue";

import Vue from "vue";
import { evEmitter } from "../ReviewLayer";
export const memoEvent = new Vue();

export function memoLayer() {
  // openlayer<script></script>
  const ol = {};
  ol.Map = Map;
  ol.View = View;
  ol.Overlay = Overlay;
  ol.Feature = Feature;
  ol.layer = olLayer;
  ol.control = olControl;
  ol.interaction = olInteraction;
  ol.proj = olProj;
  ol.source = olSource;
  ol.style = olStyle;
  ol.geom = olGeom;

  // -------------------------------------------------------------
  // 
  // -------------------------------------------------------------

  let vecLayer;

  let currentMemos = [];

  let isVisible = true;
  let isActive = false;

  let memo_popup_elm;
  let memoPop;
  let memoPos = null;
  let view_popup_elm;

  let map;
  let socketio;

  // -------------------------------------------------------------
  // Public
  // -------------------------------------------------------------

  function add() {
    map = $store.getters["amt/map"];
    socketio = $store.getters["amt/socketio"];

    $store.commit("amt/addComponent", memoLayerPopup);
    $store.commit("amt/addComponent", memoMake);

    if (map) {
      //vec_source = new ol.source.Vector({ features: [] });
      vecLayer = new ol.layer.Vector({
        source: new ol.source.Vector({ features: [] }),
      });
      // $store.commit("setMemoLayer", vecLayer);
      map.addLayer(vecLayer);
      vecLayer.setVisible(true);
      vecLayer.setZIndex(3); //@todo store

      // update();
      map.on("click", _onMapClick);

      const on_memo = (msg) => {
        //const memos = JSON.parse(msg);
        // log("RECEIVE MEMO: " + msg);

        // JSON 2022/07/25:
        // const _currentMemos = JSON.parse(msg);
        const _currentMemos = msg;

        // 
        currentMemos = _currentMemos.filter((memo) => {
          // 
          // 
          //  => 
          //  => 
          // 
          const memo_classify = $store.state.memo_classify;
          if (
            memo.classify != memo_classify &&
            "" != memo.classify &&
            memo.classify &&
            "" != memo_classify &&
            memo_classify
          ) {
            // 
            return false;
          } else {
            // 
            return true;
          }
        });
        _updateIcons(currentMemos);
      };
      socketio.on("memo", (msg) => {
        // const group_id = $store.getters["group_id"];
        // if (group_id !== 0) {
        //   on_memo(msg);
        //   console.log(msg);
        // }
        on_memo(msg);
        console.log(msg);
      });
      evEmitter.on("memo", (ev) => {
        console.log(
          "---------------------REVIEW EVENT--------------------------"
        );
        console.log(ev);
        on_memo(ev);
      });
      setTimeout(() => socketio.emit("initmemo"), 3000);
    }

    memoEvent.$on("VISIBLE", (val) => setVisible(val));
    // memoEvent.$on("ACTIVE", val => setActive(val));
    memoEvent.$on("BACK_MEMO", () => setActive(false));
    memoEvent.$on("ON_ACTIVE", () => setActive(true));
    memoEvent.$on("OFF_ACTIVE", () => setActive(false));
    memoEvent.$on("ON_VIEW", () => setVisible(true));
    memoEvent.$on("OFF_VIEW", () => setVisible(false));
    memoEvent.$on("DEL_MEMO", (memo_id) => deleteMemo(memo_id));
    memoEvent.$on("UPDATE_MEMO", (data) => updateMemo(data));
    memoEvent.$on("SEND_JSON_MEMO", (data) => send_json(data, "memo"));
    memoEvent.$on("SEND_JSON_VIDEO", (data) => send_json(data, "video"));
    memoEvent.$on("SEND_JSON_PHOTO", (data) => send_json(data, "photo"));

    memoEvent.$on("INIT_MEMO_POP", (init_memoPop) => {
      memoPop = init_memoPop;
    });
  }

  // function update() {
  //   if ($store.state.activeId.kind == "group") {
  //     socketio.emit("downMemo", String($store.state.activeId.id));
  //   }
  // }
  function update() {}

  function remove() {
    map.removeOverlay(memoPop);
    memo_popup_elm.parentNode.removeChild(memo_popup_elm);
    view_popup_elm.parentNode.removeChild(view_popup_elm);
    map.removeLayer(vecLayer);
    socketio.off("memo");
    map.un("click", _onMapClick);
  }

  function setOpacity(val) {
    //val: 0.0-1.0
    vecLayer.setOpacity(val);
  }

  function getOpacity() {
    vecLayer.getOpacity();
  }

  function setZIndex(val) {
    vecLayer.setZIndex(val);
  }

  function setVisible(val) {
    //val: true/false
    isVisible = val;
    vecLayer.setVisible(val);

    if (isVisible) {
      // update();
    } else {
      if (isActive) {
        isActive = false;
        memoPop.setPosition(undefined);
        // memoTextarea.value = "";
      }
    }
  }

  function getVisible() {
    return isVisible;
  }

  function setActive(val) {
    isActive = val;

    if (isActive) {
      // 
      $store.commit("amt/setSubMenu", "MemoLayer");
      if (!isVisible) {
        // 
        isVisible = true;
        vecLayer.setVisible(true);
        memoPop.setPosition(undefined);
      }
      memoPos = [
        $store.state.users[$store.state.myId].lng,
        $store.state.users[$store.state.myId].lat,
      ];
      const _pos = ol.proj.transform(memoPos, "EPSG:4326", "EPSG:3857");
      memoPop.setPosition(_pos);
    } else {
      if (!isVisible) {
        // 
      }
      $store.commit("amt/setSubMenu", null);
      memoPop.setPosition(undefined);
      isActive = false;
    }
  }

  function getActive() {
    return isActive;
  }

  function getUiComponent() {
    return memoLayerPopup;
  }

  function send_json(data, kind) {
    // if (isActive && memoPos && state.activeId.kind == "group") {
    if (memoPos && $store.state.activeId.kind == "group") {
      const data_json = {
        id: data.file_name,
        user_id: $store.state.myId,
        gid: $store.state.activeId.id,
        name: $store.state.users[$store.state.myId].user_name,
        lat: memoPos[1],
        lng: memoPos[0],
        date: new Date().toLocaleString(),
        kind: kind,
        info: data.info,
        status: "",
        classify:
          $store.state.memo_classify === undefined
            ? ""
            : $store.state.memo_classify,
        display_flag: 1,
      };
      // JSON 2022/07/25:
      // const msg = JSON.stringify(data_json);
      // // console.log("UP MEMO: " + msg);
      // socketio.emit("upMemo", msg);
      socketio.emit("upMemo", data_json);
    }
  }

  function updateMemo(up_memo) {
    // DB
    let memo = currentMemos.filter((m) => m.id == up_memo.id);
    if (memo) {
      // 
      currentMemos = currentMemos.filter((m) => m.id != up_memo.id);
      const memo_classify = $store.state.memo_classify;
      if (
        up_memo.classify != memo_classify &&
        "" != up_memo.classify &&
        up_memo.classify &&
        "" != memo_classify &&
        memo_classify
      ) {
        //  -> 
      } else {
        //  -> 
        currentMemos.push(up_memo);
      }
      // 
      _updateIcons(currentMemos);
    }

    // 
    // JSON 2022/07/25:
    // socketio.emit("upMemo", JSON.stringify(up_memo));
    socketio.emit("upMemo", up_memo);
  }

  function deleteMemo(memo_id) {
    // DB
    let memo = currentMemos.filter((m) => m.id == memo_id);
    if (memo) {
      // 
      currentMemos = currentMemos.filter((m) => m.id != memo_id);
      // OFF
      // memoID
      memo.display_flag = 0;
      // 
      currentMemos.push(memo);
      // 
      _updateIcons(currentMemos);
    }

    // 
    socketio.emit("delMemo", memo_id);
  }

  // -------------------------------------------------------------
  // 
  // -------------------------------------------------------------
  function logDebug(msg) {
    $store.state.logger.debug(msg);
  }
  function log(msg) {
    $store.state.logger.info(msg);
  }
  function logError(msg) {
    $store.state.logger.error(msg);
  }

  function _updateIcons(memos) {
    const iconFeatures = memos
      .filter((memo) => memo.lng != null && memo.lat != null)
      .map((memo) => {
        const coordinate = ol.proj.transform(
          [parseFloat(memo.lng), parseFloat(memo.lat)],
          "EPSG:4326",
          "EPSG:3857"
        );
        const iconFeature = new ol.Feature({
          geometry: new ol.geom.Point(coordinate),
          name: "memo",
          id: memo.id,
          uid: memo.user_id,
          gid: memo.gid,
          userName: memo.name,
          lng: memo.lng,
          lat: memo.lat,
          date: memo.date,
          kind: memo.kind,
          info: memo.info,
          display_flag: memo.display_flag,
        });
        iconFeature.setStyle(
          new ol.style.Style({
            image: new ol.style.Icon({
              anchor: [0.5, 1],
              opacity: 1.0,
              scale: 0.25,
              src:
                memo.status == ""
                  ? memo.kind == "memo"
                    ? "/images/memo_pin.png"
                    : memo.kind == "photo"
                    ? "/images/photo_pin.png"
                    : "/images/video_pin.png"
                  : memo.kind == "memo"
                  ? "/images/memo_red_pin.png"
                  : memo.kind == "photo"
                  ? "/images/photo_red_pin.png"
                  : "/images/video_red_pin.png",
            }),
          })
        );
        return iconFeature;
      });
    const source = vecLayer.getSource();
    for (const feature of source.getFeatures()) {
      source.removeFeature(feature);
    }
    source.addFeatures(iconFeatures);
  }

  // function show_pop_first() {
  //   const pos = [state.users[state.myId].lng, state.users[state.myId].lat]
  //   memoPop.setPosition(pos);
  //   memoPos = ol.proj.transform(pos, "EPSG:3857", "EPSG:4326");
  // }

  function pos_rounddown(pos) {
    const digit = 6;

    // 7
    return Math.round(pos * Math.pow(10, digit)) / Math.pow(10, digit);
  }

  function _onMapClick(e) {
    if (isActive && $store.state.activeId.kind == "group") {
      memoPop.setPosition(e.coordinate);
      memoPos = ol.proj.transform(e.coordinate, "EPSG:3857", "EPSG:4326");
      memoPos[0] = pos_rounddown(memoPos[0]);
      memoPos[1] = pos_rounddown(memoPos[1]);
      // memoKind = "memo";
    } else {
      map.forEachFeatureAtPixel(e.pixel, (feature, layer) => {
        _onIconClick(e, feature);
      });
    }
  }

  ///////////////////
  // 
  ///////////////////

  let memo;
  function _onIconClick(e, feature) {
    if (feature.get("name") == "memo") {
      $store.state.sound_on();
      const id = feature.get("id");
      const kind = feature.get("kind");
      // playId = id;
      // playKind = kind;
      // const memo = currentMemos.find(m => m.id == id);
      memo = currentMemos.find((m) => m.id == id);
      if (memo) {
        memo.kind = kind;
      } else {
        return;
      }
      memo.coordinate = e.coordinate;

      // 
      // 
      //  => 
      //  => 
      // 
      const memo_classify = $store.state.memo_classify;
      if (
        memo.classify != memo_classify &&
        "" != memo.classify &&
        memo.classify &&
        "" != memo_classify &&
        memo_classify
      ) {
        // 
        return;
      }
      if (0 == memo.display_flag) {
        // 
        return;
      }

      // 
      let memo_info = {};
      // ID
      memo_info.id = memo.id;
      // 
      memo_info.click = function (memo_id) {
        // 
        memoEvent.$emit("MEMO_VIEW2", $store.state.memo.clicked_memo[memo_id]);
        // 
        $store.commit("hiddenClickList");
      };
      // 
      memo_info.param = memo.id;
      // 
      if (memo.kind == "video") {
        memo_info.type = "img";
        memo_info.img = "/memo/" + memo_info.id + "_thumb.jpg";
      } else if (memo.kind == "photo") {
        memo_info.type = "img";
        memo_info.img = "/memo/" + memo.id + ".jpg";
      } else {
        memo_info.type = "text";
        memo_info.text = memo.info;
      }
      // 
      let memo_image =
        memo.status == ""
          ? memo.kind == "memo"
            ? "/images/memo_icon.png"
            : memo.kind == "photo"
            ? "/images/photo_icon.png"
            : "/images/video_icon.png"
          : memo.kind == "memo"
          ? "/images/memo_red_icon.png"
          : memo.kind == "photo"
          ? "/images/photo_red_icon.png"
          : "/images/video_red_icon.png";
      memo_info.icon = memo_image;
      // 
      memo_info.info = memo.date;

      // store
      // $store.state.click_targets.push(memo_info);
      $store.commit("addClickTargets", memo_info);
      $store.commit("displayClickList");

      // 
      $store.state.memo.clicked_memo[memo.id] = memo;
    }
  }

  function _uuidv4() {
    return "xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx".replace(
      /[xy]/g,
      function (c) {
        var r = (Math.random() * 16) | 0,
          v = c == "x" ? r : (r & 0x3) | 0x8;
        return v.toString(16);
      }
    );
  }

  // -------------------------------------------------------------
  //  
  // -------------------------------------------------------------

  return {
    add,
    remove,
    update,
    setOpacity,
    getOpacity,
    setZIndex,
    setVisible,
    getVisible,
    setActive,
    getActive,
    getUiComponent,
    // send_json,
    // get_memo_content
  };
}
