import $store from "@/store";
import chatLayer from "./ChatLayer.vue";

import Vue from "vue";
export const chatEvent = new Vue();

export function chatLayerModule() {
  const noop = () => {};

  function add() {
    $store.commit("amt/addComponent", chatLayer);
  }

  function setOpacity(val) {
    chatEvent.$emit("SETOPACITY", val);
  }

  function setVisible(val) {
    chatEvent.$emit("SETVISIBLE", val);
  }

  function setZIndex(val) {
    chatEvent.$emit("SETZINDEX", val);
  }

  function setActive(val) {
    chatEvent.$emit("SETACTIVE", val);
  }

  return {
    add, // 
    remove: noop, // 
    update: noop, // 
    setOpacity,
    setVisible,
    setZIndex,
    setActive, // 
  };
}
